import React from "react";
import { makeStyles } from "@material-ui/core";

import HeroList from "./HeroList";

const useStyles = makeStyles({
    container: {
        position: "relative",
        height: "250px",
    }
});

type HeroSelectorProps = {
    onSelectionChanged?: (ids: string[]) => void;
    // encounterId: string | null;
    chooseAll?: boolean;
    multiSelect?: boolean;
};

/*
* The selection of heroes one the menu bar
*/
const HeroSelector: React.FunctionComponent<HeroSelectorProps> = (props: HeroSelectorProps) => {
    const classes = useStyles();

    return (
        <div className={classes.container}>
            <HeroList chooseAll={props.chooseAll} multiSelect={props.multiSelect} onSelectionChanged={props.onSelectionChanged} />
        </div>
    );
}

export default HeroSelector;
